import React from "react"
import { Link } from "gatsby"

import Layout from "../components/layout"
import SEO from "../components/seo"
import Sidebar from "../components/sidebar"
import MailList from "../components/maillist"

const AboutPage = () => (
  <Layout>
    <SEO title="About Us" />
    <div class="col-md-4" id="content-desktop">
      <Sidebar />
    </div>
    <div class="col-md-8">
      <br />

      <div class="alert alert-secondary" role="alert">
        <h2>
          من نحن <i class="fas fa-address-card"></i>
        </h2>
      </div>

      <Link to="/" class="btn btn-secondary btn-sm">
        <i class="far fa-arrow-alt-circle-left"></i> العودة للخلف
      </Link>
      <br />
      <br />
      <div id="content-mobile">
        <div class="p-3 mb-2 bg-success text-white">القائمة البريدية</div>
        <MailList />
      </div>
      <hr />
    </div>
  </Layout>
)

export default AboutPage
